export default {
	data() {
		return {
			title: '',
			answer: '',
			lib: [],
			testIndex: 0
		}
	},
	onLoad() {
		let self = this
		uni.getStorage({
			key: 'test',
			success: function (res) {
				console.log(res);
				if(res.data instanceof Array && res.data.length > 0){
					console.log(res.data)
					self.lib = res.data
					self.testIndex = Math.floor(Math.random() * res.data.length)
					self.title = res.data[self.testIndex].title
					self.answer = res.data[self.testIndex].answer
				} else {
					self.title = ''
					self.answer = ''
				}
			}
		});
	},
	onShow() {
		let self = this
		uni.getStorage({
			key: 'test',
			success: function (res) {
				console.log(res);
				if(res.data instanceof Array && res.data.length > 0){
					// console.log()
					self.lib = res.data
				}
			}
		});
	},
	methods: {
		checkAnswer(){
			uni.navigateTo({
			    url: '/pages/checkAnswer/checkAnswer?index=' + this.testIndex
			});
		},
		changeATest(){
			this.testIndex = Math.floor(Math.random() * this.lib.length)
			this.title = this.lib[this.testIndex].title
			this.answer = this.lib[this.testIndex].answer
		}
	}
}
